/* Base styles - light mode unchanged */
html, body {
  height: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

/* Light mode (original) */
body {
  background: linear-gradient(to bottom, #f9fafb, #ffffff);
  color: #111827; /* gray-900 */
}

/* Dark mode body */
.dark body {
  background: linear-gradient(to bottom, #000000, #0f172a); /* black â†’ slate-950 */
  color: #f9fafb; /* white/light text */
}

/* Cards/sections in light mode (original) */
.bg-white,
.bg-gray-50,
.bg-blue-50,
.bg-blue-100,
section {
  background-color: #ffffff;
}

/* Dark mode cards/sections - black bg + white text + subtle border */
.dark .bg-white,
.dark .bg-gray-50,
.dark .bg-blue-50,
.dark .bg-blue-100,
.dark section,
.dark .rounded-2xl,
.dark .shadow-xl {
  background-color: #000000 !important;
  color: #f9fafb !important;
  border: 1px solid #334155 !important; /* subtle slate-700 border */
}

/* Text contrast fixes - only where needed */
.text-gray-900 { color: #111827; }
.dark .text-gray-900 { color: #f9fafb; }

.text-gray-700 { color: #374151; }
.dark .text-gray-700 { color: #d1d5db; }

.text-gray-600 { color: #4b5563; }
.dark .text-gray-600 { color: #9ca3af; }

.text-gray-500 { color: #6b7280; }
.dark .text-gray-500 { color: #9ca3af; }

.text-blue-900 { color: #1e3a8a; }
.dark .text-blue-900 { color: #bfdbfe; }

.text-green-600 { color: #16a34a; }
.dark .text-green-600 { color: #86efac; }

.text-yellow-400 { color: #facc15; }
.dark .text-yellow-400 { color: #fde047; } /* keep yellow stars in dark */

/* Footer - light: original, dark: black */
footer {
  background-color: #ffffff; /* original light footer */
  color: #111827;
}

.dark footer {
  background-color: #000000;
  color: #ffffff;
}

/* Header scrolled - light: original, dark: black */
header.scrolled {
  background-color: transparent; /* or your original */
}

.dark header.scrolled {
  background-color: #000000 !important;
}

/* Mobile drawer text - keep original colors (no forced gold/yellow) */
.text-yellow-300 { color: #fde047; } /* if you still have it somewhere */
.dark .text-yellow-300 { color: #f3f4f6; } /* light gray in dark */

/* Hover states - original */
a:hover,
button:hover {
  color: #22c55e !important;
}

.dark a:hover,
.dark button:hover {
  color: #86efac !important;
}

/* Custom marker (HQ symbol stays as you wanted) */
.custom-marker-head {
  background-color: #dc2626 !important;
  border: 3px solid #991b1b !important;
  color: white !important;
  font-weight: bold !important;
  width: 36px !important;
  height: 36px !important;
  border-radius: 50% !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  font-size: 14px !important;
  box-shadow: 0 2px 6px rgba(0,0,0,0.4) !important;
}

/* IMPORTANT: No forced white on all icons/symbols - revert to original behavior */
svg,
[data-lucide],
.lucide-icon {
  /* No !important color overrides here - let original classes handle it */
}

/* Social icons - original colors */
a[href*="facebook"],
a[href*="tiktok"],
a[href*="youtube"] svg {
  color: inherit; /* inherit from parent */
}

/* Social icons in light mode - white / light background */
a[href*="facebook"] svg,
a[href*="tiktok"] svg,
a[href*="youtube"] svg {
  color: #ffffff !important; /* white icons */
  fill: #ffffff !important;
  background-color: transparent !important; /* no dark bg */
}

/* Ensure no dark background on social links in light mode */
a[href*="facebook"],
a[href*="tiktok"],
a[href*="youtube"] {
  background-color: transparent !important;
  border: none !important;
}

/* Hover state - keep green hover, but icons stay white */
a[href*="facebook"]:hover svg,
a[href*="tiktok"]:hover svg,
a[href*="youtube"]:hover svg {
  color: #22c55e !important; /* green-500 on hover */
  fill: #22c55e !important;
}

/* In dark mode - keep icons white/light as before */
.dark a[href*="facebook"] svg,
.dark a[href*="tiktok"] svg,
.dark a[href*="youtube"] svg {
  color: #ffffff !important;
  fill: #ffffff !important;
  background-color: transparent !important;
}
